            <button  class="absolute right-0 -mt-64 mr-12" @click="fullscreenDialog = true">
              <i class="fas fa-edit text-3xl text-gray-800 hover:text-blue-500"></i>
            </button>
            <v-easy-dialog v-model="fullscreenDialog"  >
              <div class="bg-white overflow-y-auto">
                <div class="flex flex-col">
                  <div class="text-center mt-12 text-3xl font-semibold text-gray-800">About company!</div>
                  <div class="flex justify-end space-x-2 mr-8">
                    <button class="outline-none" @click="fullscreenDialog = false">
                      <i class="fas fa-times-circle text-3xl text-gray-800"></i>
                    </button>
                  </div>
                </div>
                <div class="flex flex-col px-24">
                  <textarea v-model="h1" class="w-96 border shadow-lg border-gray-800 rounded-lg h-40 pl-3 pt-2 mt-8  " placeholder="Your Message" type="text"></textarea>
                  <textarea v-model="h2" class="w-96border shadow-lg border-gray-800 rounded-lg h-40 pl-3 pt-2 mt-8  " placeholder="Your Message" type="text"></textarea>
                  <textarea v-model="h3" class="w-96 border shadow-lg border-gray-800 rounded-lg h-40 pl-3 pt-2 mt-8  " placeholder="Your Message" type="text"></textarea>
                  <button @click.prevent="submitHandler1" class="mt-12 mb-16 bg-blue-500 h-12 rounded-md text-white transition font-semibold text-xl w-64 mx-auto hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50 ">Submit</button>
                </div>
              </div>   
            </v-easy-dialog>
          </div>
        </div>
      </div>
    </div>

    <div class="section-4"> 
      <div class="container mx-auto px-4 md:px-4 lg:px-0 xl:32">
        <button class="absolute right-0 -mt-4 mr-12" @click="fullscreenDialog2 = true">
          <i class="fas fa-edit text-3xl text-gray-800 hover:text-blue-500"></i>
        </button>
        <v-easy-dialog v-model="fullscreenDialog2">
          <div class="bg-white overflow-y-auto">
            <div class="flex flex-col">
              <div class="text-center mt-12 text-3xl font-semibold text-gray-800">About US!</div>
              <div class="flex justify-end space-x-2 mr-8">
                <button @click="fullscreenDialog2 = false">
                  <i class="fas fa-times-circle text-3xl text-gray-800"></i>
                </button>
              </div>
            </div>
            <div class="flex flex-col px-24">
              <textarea v-model="h4" class="w-96 border shadow-lg border-gray-800 rounded-lg h-40 pl-3 pt-2 mt-8  focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent" placeholder="Your Message" type="text"></textarea>
              <textarea v-model="h5" class="w-96 border shadow-lg border-gray-800 rounded-lg h-40 pl-3 pt-2 mt-8  focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent" placeholder="Your Message" type="text"></textarea>
              <button @click.prevent="submitHandler3" class="mt-12 mb-16 bg-blue-500 h-12 rounded-md text-white font-semibold text-xl w-64 mx-auto hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50 ">Submit</button>
            </div>
          </div>   
        </v-easy-dialog>
