          <div class="mt-10">
            <div class="border-l-4 border-blue-500">
              <h4 class="text-gray-800 pl-4 text-xl xl:text-2xl lg:text-2xl md:text-2xl">{{ h4 }}</h4>
            </div>
          </div>
          <div class="mt-10">
            <div class="border-l-4 border-blue-500">
              <h4 class="text-gray-800 text-xl xl:text-2xl lg:text-2xl md:text-2xl pl-4">{{ h5 }}</h4>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="section-5 container mx-auto my-12">
      <h4 class="text-xl font-semibold">About Header</h4>
      <div class="grid grid-cols-6">
        <div class="col-span-4">
          <textarea v-model="info.first.h2" class="w-full  border shadow-lg border-gray-800 rounded-lg h-32 px-3 py-4 mt-8  focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent" placeholder="Your Message" type="text"></textarea>
        </div>
        <div class="col-span-2 text-center pt-16">
          <button @click="submitFirst" class="bg-blue-500 text-white py-2 px-6 rounded-md">Submit</button>
        </div>
      </div>
    </div>
    <div class="section-6 container mx-auto my-12">
      <h4 class="text-xl font-semibold">Apply Header</h4>
      <div class="grid grid-cols-6">
        <div class="col-span-4">
          <textarea v-model="info.second.h2" class="w-full  border shadow-lg border-gray-800 rounded-lg h-32 px-3 py-4 mt-8  focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent" placeholder="Your Message" type="text"></textarea>
        </div>
        <div class="col-span-2 text-center pt-16">
          <button @click="submitSecond" class="bg-blue-500 text-white py-2 px-6 rounded-md">Submit</button>
        </div>
      </div>
    </div>
    <div class="section-7 container mx-auto my-12">
      <h4 class="text-xl font-semibold">Youtube Url</h4>
      <div class="grid grid-cols-6">
        <div class="col-span-4">
          <p class="mt-5">https://www.youtube.com<span class="text-blue-500">/embed/</span>tgbNymZ7vqY</p>
          <input v-model="info.first.url" type="text" class="w-full  border shadow-lg border-gray-800 rounded-lg h-10 px-3 py-4 mt-4 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent">
        </div>
        <div class="col-span-2 text-center pt-14">
          <button @click="submitUrl" class="bg-blue-500 text-white py-2 px-6 rounded-md">Submit</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import EditNavbar from '../components/EditNavbar'
import VEasyDialog from 'v-easy-dialog'
export default {
  data: () => ({
    home: [],
    info: [],
    fullscreenDialog: false,
    fullscreenDialog2: false,
    h1: '',
    h2: '',
    h3: '',
    h4: '',
    h5: '',
    h6: '',
  }),
  async mounted() {
    this.home = await this.$store.dispatch('fetchHome')
    this.info = await this.$store.dispatch('fetchMainHeader')

    this.h1 = this.home.company.h1
    this.h2 = this.home.company.h2
    this.h3 = this.home.company.h3   
    this.h4 = this.home.about.h1
    this.h5 = this.home.about.h2
    this.h6 = this.home.image.first.name
  },
  methods: {
    async submitHandler1() {
      const categoryData = {
        h1: this.h1,
        h2: this.h2,
        h3: this.h3,
      }
      await this.$store.dispatch('updateProduct1', categoryData)
      this.fullscreenDialog = false

    },
    async submitHandler3() {
      const categoryData = {
        h1: this.h4,
        h2: this.h5,
      }
      await this.$store.dispatch('updateProduct3', categoryData)
      this.fullscreenDialog2 = false
    },
    async submitFirst() {
      const formData = {
        h2: this.info.first.h2
      }
      await this.$store.dispatch('updateFirst', formData);
    },
    async submitSecond() {
      const formData = {
        h2: this.info.second.h2
      }
      await this.$store.dispatch('updateSecond', formData);
    },
    async submitUrl() {
      const formData = {
        url: this.info.first.url
      }
      await this.$store.dispatch('updateUrl', formData);
    },
  },
  components: {
    EditNavbar,
    VEasyDialog
  }
}
</script>
